.container {
    position: relative;
    text-align: center;
    width: 100%;
}

.svg-container {
    position: absolute;
    top: 24.9%;
    left: 50%;
    transform: translate(-50%, -50%);
}


.back-svg {
    width: 600px;
}

.container svg {
    transform: scale(0.42);
}

